// font-family: 'Open Sans', sans-serif;
// font-family: 'Roboto', sans-serif;
// font-family: 'Yellowtail', cursive;
$primary-color:#274C5B;
$secondary-color:#7EB693;
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
}
body {
    font-family: 'Roboto','Arial', sans-serif;
    font-size: 18px;
    font-weight: 400;
}
img {
    object-fit: contain;
    width: 100%;
}
.wrapper {
    max-width: 1440px;
    padding: 0 30px;
    margin: 0 auto;
}
.button {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    cursor: pointer;
    font-size: 17px;
}
.section-title {
    font-family: 'Yellowtail';
    font-style: normal;
    font-weight: 400;
    // font-size: 36px;
    color: $secondary-color;
    margin-bottom: 10px;
    text-align: center;
}
.section-subtitle {
    font-weight: 700;
    font-size: 45px;
    color: $primary-color;
    margin-bottom: 20px;
    text-align: center;
}
//! header
.header .wrapper{
    color: $primary-color;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    min-height: 100px;
}
.header-nav {
    margin-left: auto;
}
.nav-list {
    display: flex;
    flex-wrap: wrap;
}
.nav__item:not(:last-child) {
    margin-right: 25px;
}
.nav__link {
    color: inherit;
    padding-bottom: 10px;
}
.nav__link--active {
    color: $secondary-color;
    cursor: default;
}
.nav__link:not(.nav__link--active):hover,
.nav__link:not(.nav__link--active):focus {
    border-bottom: 2px solid $primary-color;
    width: 100%;
}
.header__cart {
    margin-left: auto;
    width: 160px;
    min-height: 70px;
    border: 1px solid #D4D4D4;
    border-radius: 33px;
    padding: 5px 11px;
    column-gap: 10px;
    color: inherit;
}
.cart-icon {
    background-color: $primary-color;
    border-radius: 50%;
    width: 56px;
    height: 56px;
    position: relative;
}
.cart-icon::before {
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url(../img/cart.svg);
    width: 26px;
    height: 24px;
}
//! header END
//! slider 
.slider .wrapper{
    display: flex;
    flex-wrap: wrap;
    background-image: url(../img/slider.png);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #F2F0F1;
    width: 100%;
    height: 715px;
    margin-bottom: 140px;
}
.slider__content {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 40%;
    text-align: center;
}
.slider__made {
    font-family: 'Yellowtail';
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 65px;
    letter-spacing: 0.36px;
    text-transform: capitalize;
    color: #7EB693;
    margin-bottom: 10px;
}
.slider__title {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 70px;
    line-height: 84px;
    letter-spacing: 0.36px;
    color: #274C5B;
    margin-bottom: 20px;
}
.slider__content--button {
    background-color: #EFD372;
    width: 220px;
    min-height: 80px;
    color: #274C5B;
    border: none;
    border-radius: 15px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 19px;
    line-height: 32px;
}
.slider__arrow-icon {
    background-color: #274C5B;
    background-image: url(../img/arrow.svg);
    background-position: center;
    border-radius: 50%;
    width: 15px;
    height: 11px;
    padding: 15px;
    background-repeat: no-repeat;
    margin-left: 20px;
}
//! slider END

//! offer 
.offer .wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.offer .wrapper > div {
    text-align: center;
}
.offer__fruits {
    
}
//! offer END

//! our product
.product-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(390px, 1fr));
    gap: 20px;
    margin-bottom: 130px;
    padding-top: 20px;
}
.product__item {
    border-radius: 30px;
    padding: 30px;
    background-color: #F9F8F8;
    max-width: 445px;
    min-height: 590px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
}
.product__category {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    color: #fff;
    width: 120px;
    min-height: 40px;
    background-color: $primary-color;
    font-family: 'Open Sans', 'Arial', sans-serif;
    font-weight: 700;
    font-size: 15px;
    line-height: 27px;
}
.product__title {
    color: $primary-color;
    font-size: 22px;
    font-weight: 400;
}
.product-price {
    border-top: 1px solid #D4D4D4;
    padding-top: 20px;
    font-family: 'Open Sans', 'Arial', sans-serif;
    font-size: 18px;
    letter-spacing: 0.36px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
.price__old {
    text-decoration: line-through;
    font-weight: 400;
    color: #B8B8B8;
    margin-right: 10px;
}
.price__new {
    font-weight: 700;
    color: $primary-color;
}
.price__star {
    display: inline-block;
    background-image: url(../img/star.svg);
    background-repeat: repeat-x;
    width: 70px;
    height: 19px;
    margin-left: auto;
}
//! our product END
